export default {
	props:{
		item:Object,
		index:Number
	},
	data() {
		return {
			isGuanZhu:this.item.isGuanZhu
		}
	},
	methods:{
		//点击关注
		guanZhu(){
			this.isGuanZhu=true;
		uni.showToast({
			title: '关注成功',
			icon:'success'
		});
		},
		img(k){//点击图片，显示预览图片
			uni.previewImage({
				urls:this.item.morePic,
				current:k
			})
		}
	}
}
